@keyframes bounce{
  0%{ transform: scale(1.2);}
  
  100%{transform: scale(1.0);}
}

.cell{
    float: left;
    font-size: 20px;
    font-weight: bold;
    line-height: 26px;
    height: 26px;
    margin-right: 0px;
    margin-bottom: 0px;
    margin-top: 0px;
    margin-left: 0px;
    padding: 0;
    text-align: center;
    width: 26px;

}
.cell:focus{
  outline: none;
}
.open-cell {
    background: #ccc;
    border: 1px solid darkcyan;
  }
.wall-cell {
    background: darkcyan;
    border: 1px solid darkcyan;
    animation-name: bounce;
    animation-duration: .25s;
  }
  .wall-cell-seen {
    background: #005858;
    border: 1px solid #005858;
  }
.start-cell {
    background: rgb(0, 212, 0);
    border: 1px solid rgb(0, 212, 0);
  }
.goal-cell {
    background: #f00;
    border: 1px solid #f00;
    }
.oList-cell {
    background: #05b;
    border: 1px solid #05b;
   }
  .cList-cell {
    background: #099;
    border: 1px solid #099;
    animation-name: bounce;
    animation-duration: .25s;
  }  
  .cList-cell-w {
    background: #00f;
    border: 1px solid #00f;
    animation-name: bounce;
    animation-duration: .25s;
  }
  .projected-path-cell{
    background: rgb(255, 199, 114);
    border: 1px solid rgb(255, 199, 114);
    animation-name: bounce;
    animation-duration: .25s;
  }
  .projected-path-cell-w{
    background: rgb(255, 153, 0);
    border: 1px solid rgb(255, 153, 0);
    animation-name: bounce;
    animation-duration: .25s;
   }
  .traversed-cell {
    background: rgb(208, 247, 67);
    border: 1px solid rgb(208, 247, 67);
    animation-name: bounce;
    animation-duration: 1s;
  }  
  .traversed-cell-goal {
    background: rgb(255, 192, 57);
    border: 1px solid rgb(255, 192, 57);
  }  .final-path-cell {
    background: rgb(87, 255, 81);
    border: 1px solid rgb(87, 255, 81);
    animation-name: bounce;
    animation-duration: .25s;
  }